header {
  height: 100vh;
  padding-top: 3rem;
  overflow: hidden;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* -----------CTA------------- */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

.btn-cv:hover {
  background: var(--color-primary);
  color: var(--color-bg);
  border-color: var(--color-bg);
}

/* -----------HEADER SOCIALS------------- */
.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 8rem;
}

.header__socials::after {
  content: '';
  width: 1px;
  height: 2rem;
  background: var(--color-primary);
}

/* -----------ME------------- */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  /* width: 22rem; */
  width: 24rem;

  height: 24rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  /* border-radius: 12rem 12rem 0 0; */
  border-radius: 12rem;
  overflow: hidden;
  padding-top: 1.5rem;
}

/* -----------CSROLLDOWN------------- */

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 10rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/* ---------------MEDIA QUERIES(MEDIUM DEVICES)----------------- */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ---------------MEDIA QUERIES(SMALL DEVICES)----------------- */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }
  .header__socials,
  .scroll__down {
    display: none;
  }
}
